<template>
    <div v-if="checkPublicLinkExpired==false">
        <div class="theme-primary partition p-actions-expand tablehead p-actions-collapse mb-3">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left text-right p-0 theme-light-shade">
                    <a href="https://talygen.com/" style="float: left;margin-left: 10px;margin-top: 3px;">
                        <img src="/Content/images/footer-light-logo.png" alt="footer-logo">
                    </a>
                    <span class="p-actions float-right">
                        <a href="javascript:;" @click="DownloadFile" class="p-action-btn new text-primary" 
                            data-toggle-tooltip="tooltip" data-original-title="Card View" title="Download All">                        
                              <em class="fa fa-download"></em>  
                        </a>
                        <a href="javascript:;" v-bind:class="{ 'p-action-btn new text-secondary': true, 'disabled': false }" title="Card View" data-toggle-tooltip="tooltip" data-original-title="Card View"
                            v-on:click="listType = 'CardView';" v-if="listType == 'ListView'">
                            <em class="fa fa-th-large"></em>                        
                        </a>
                        <a href="javascript:;" v-bind:class="{ 'p-action-btn new text-secondary': true, 'disabled': false }" title="List View"
                            data-toggle-tooltip="tooltip" data-original-title="List View"
                            v-on:click="listType = 'ListView'; " v-if="listType == 'CardView'">
                            <em class="fa fa-list"></em> 
                        </a>
                    </span>
                </div>
        </div>
        <div class="col-md-12 p-0 float-left">
            <div class="heading-border new mb-3">
                <h5 class="h5">
                    <span class="font-weight-light mr-2">{{$t('YouAreInside')}}</span>
                    <span class=" border-0 font-18" v-for="(item, index) in parentname" :class="{'disabled':parentname.length-1==index}">
                        <a v-if="parentname[0] != ''" :class="{'disabled text-disabled':parentname.length-1==index, 'text-primary':parentname.length-1!=index}" @click="SpecificFolder(item[1], commonParentName)"
                                :folderId="item[1]" >  {{ item[0]}} >
                        </a>
                    </span>
                    <small class="float-right"></small>
                </h5>
            </div>
        </div>
        <div class="listing">
                <div class="table-responsive table-fix-header" id="div_forListView" v-if="listType == 'ListView'">
                    
                        <table class="table table-bordered mt-0" style="width: 100%;"
                            id="tblFileList">
                            <thead class="thead-bg">
                                <tr>
                                    <th>
                                        <span>Names</span>
                                    </th>
                                    <th class="text-center">
                                        <span>Size</span>
                                    </th>
                                    <th class="text-center">
                                        <span>Created On</span>
                                    </th>
                                    <th class="text-center">
                                        <span>Action</span>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
    
                                <tr v-for="item in folderFileListing">
                                        <td :class="(item.IsDocCode == 1 ? 'file-image doc-icon' : '')">
                                        <div class="pin pr-2 d-inline-block">
                                            <em style="cursor:pointer;" :pinId="item.PinId" class="fa fa-thumb-tack"
                                                @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>
                                        </div>
                                        <em class="fa fa-folder pr-2 font-18 text-primary" style="font-size:25px;" v-if="item.IsDocCode == 0"
                                            aria-hidden="true"></em>
                                        <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 0">
                                            {{ item.pin_id }}
                                            <a class="aFileDownload" :value="item.Id"
                                                @click="GetFolderList(item.Id, commonParentName, '', 'a' + item.Id)"
                                                v-bind:folderId="item.Id" v-bind:filepath="item.FilePath"
                                                v-bind:filetype="item.FileType" v-bind:filename="item.FileName"
                                                href="javascript:;" title="">{{ item.NAME }}</a>
                                            <!-- <em class="fa fa-thumb-tack" @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>  -->
                                        </span>
                                        <img style="height: 30px;" v-if="item.DocType != '' && (item.DocExtention=='pdf' || item.DocExtention=='pdf' || item.DocExtention=='jpg' || item.DocExtention=='png' || item.DocExtention=='jpeg')" :class="GetFileIconClass(item.DocExtention)"
                                        :alt='item.FileType' :title='item.DocType' :data-fileType='item.DocExtention' :src="GetThumbnail(item.FileThumbnailPath)" 
                                        @click="FilesDetail(item.Id, item.IsDocCode, item.DocPathLocation, item.DocExtention, item.ParentId, item.PERMISSION)"/>
                                        <em v-if="item.IsDocCode == 1 && (item.DocExtention=='txt' || item.DocExtention=='docx'|| item.DocExtention=='xlsx' || item.DocExtention=='xls')" :class="GetFileIconClass(item.DocExtention)" 
                                            width="27px"></em> &nbsp;
                                        <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 1">
                                            <a class="aFileDownload" :value="item.Id" v-bind:doccode="item.IsDocCode"
                                                v-bind:folderId="item.Id"
                                                @click="FilesDetail(item.Id, item.IsDocCode, item.DocPathLocation, item.DocExtention, item.ParentId)"
                                                v-bind:filepath="item.FilePath" v-bind:filetype="item.FileType"
                                                v-bind:filename="item.FileName" href="javascript:;" title="">{{ item.NAME }}
                                            </a>
                                        </span>
                                    </td>
                                    <td class="text-center">
                                        <span class="text-center text-overflow-dynamic-container">
                                            {{ convertmbtoKb(item.DocSize) }}
                                        </span>
                                    </td>
                                    <td class="text-center">
                                        <span class="text-center text-overflow-dynamic-container">
                                            {{ $options.filters.formatDate(item.created_at) }}
                                        </span>
                                    </td>
                                    <td class="text-center single-action">
                                        <a @click="DownloadSingleFiles(item.Id,item.NAME)" class="text-center text-overflow-dynamic-container action-icon">
                                        <em class="fa fa-download text-info" v-if="item.IsDocCode==1"></em>
                                        </a>
                                    </td>
                                </tr>
                                <tr>
                                    <td v-if="folderFileListing.length == 0" colspan="7" class="text-danger text-center no-record">{{$t('NoRecordfound')}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div v-if="listType == 'CardView'" class="row card-view bg-border custom-box-width" id="div_forCardView">
                        <div v-for="file in folderFileListing" :key="file"
                            class='col-sm-12 col-lg-2 col-xl-2 col-md-12 mb-3 div-cardView'>
                            <div class='box docattachment border'>
                                <div class="verticle-action">
                                    <em class="fa fa-download text-info" @click="DownloadSingleFiles(file.Id,file.NAME)" v-if="file.IsDocCode==1"></em>
                                </div>
                                <div class="pinff pinnewtag pr-2 d-inline-block">
                                    <em class="fa fa-thumb-tack" @click="MarkAsUnPin(file.Id)" v-if="file.PinId > 0"
                                        aria-hidden="true"></em>
                                </div>
    
                                <div class='img-icons new text-center p-2 file-image doc-icon'>
                                <img  style="height: 85px;" v-if="file.DocType != '' && (file.DocExtention=='pdf' || file.DocExtention=='jpg' || file.DocExtention=='png' || file.DocExtention=='jpeg')" :class="GetFileIconClass(file.DocExtention)"
                                        :alt='file.FileType' :title='file.DocType' :data-fileType='file.DocExtention' :src="GetThumbnail(file.FileThumbnailPath)" 
                                        @click="openLightbox"/>
                                    <em style="height: 85px;" v-if="file.DocExtention=='xls'  || file.DocExtention=='xlsx' ||  file.DocExtention=='txt' || file.DocExtention=='docx'" :class="GetFileIconClass(file.DocExtention)"
                                        :alt='file.FileType' :title='file.DocType' :data-fileType='file.DocExtention'
                                        @click="FilesDetail(file.Id, file.IsDocCode, file.DocPathLocation, file.DocExtention, file.ParentId, file.PERMISSION)"></em>
                                    <em style="height: 85px;" v-if="file.IsDocCode==0" class='fa fa-folder pr-2 font-18 text-primary' :alt='file.FileType'
                                        :title='file.DocType' @click="GetFolderList(file.Id, commonParentName)"
                                        :data-fileType='file.DocExtention'></em>
                                </div>
                                <div class='row mx-0 mb-3  align-items-end'>
                                    <div class='col-md-12 font-14 text-center'>
                                        <span class="text-overflow-dynamic-container text-truncate" :title="file.NAME"
                                            v-if="file.IsDocCode == 0">
                                            {{ file.pin_id }}
    
                                            <span style='display:block;  padding-bottom:5px;' :title='file.FileName'>
                                                {{ file.NAME }}
                                            </span>
                                        </span>
    
                                        <span class="text-overflow-dynamic-container text-truncate" :title="file.NAME"
                                            v-if="file.IsDocCode == 1">
                                            <span style='display:block; padding-bottom:5px;' :title='file.FileName'>
                                                {{ file.NAME }}
                                            </span>
                                        </span>
                                    </div>
                                    <div class='col-md-12 font-14 text-center' v-if="file.DocType != ''">
                                        <span>{{convertmbtoKb(file.DocSize)}} </span> |
                                        <span>{{$options.filters.formatDate(file.created_at)}}</span>
                                    </div>
                                    <div class='col-md-12 font-14 text-center' v-else>
                                    <em aria-hidden="true"></em>{{ file.FileCount }} Files |
                                        <em aria-hidden="true"></em>{{ convertmbtoKb(file.DocSize) }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="border-top-pagi">
                        <Pager v-bind:numberofrecords="totalRecords" prev-text="Prev" v-on:change="pageSizecommon"
                        first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
                    </div>
        </div>
    </div> 
    <div v-else>
        <div class="col-md-12 p-0 m-auto text-center">
            <title>Link Expired</title>
            <body>
                <div class="content">
            <h1>Link Expired</h1>
            <p>We're sorry, but the link you are trying to access has expired.</p>
                </div>
             </body>                                  
        </div>
    </div>      
    </template>
    <script>
    import DataService from '../../services/DataService';
    import Pager from "../../components/Common/Pager.vue"
    import VueEasyLightbox from 'vue-easy-lightbox'
    export default{
        components:{
            Pager,
            VueEasyLightbox
        },
        created: function(){
            debugger
            this.ExpirationDate=this.$route.query.ExpirationDate;
            this.GetFolderList();
            var decodeId = atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''));
            var StorageFileId = parseInt(decodeId);
            this.BreadCrumb("MySpace",StorageFileId);
            this.$router.push({ path: "/DownloadPublicFolder", query: { FileId: btoa(this.StorageFileId), UniqueCode:window.location.search.split("UniqueCode=")[1]} });
        },
        data(){
            return{
                folderFileListing:[],
                parentname:[],
                totalRecords:null,
                pageNo:1,
                pageSize:10,
                listType:'ListView',
                visibleRef:false,
                ExpirationDate:null,
                checkPublicLinkExpired:false
            }
        },
        methods:{
            GetFolderList: function(id){
                var vm=this;
                if(id!=null){
                    vm.StorageFileId=id
                }
                else{
                    if(id!=undefined){
                        vm.StorageFileId=id
                    }
                    else{
                        var decodeId = atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''));
                        vm.StorageFileId = parseInt(decodeId);
                    }
                   
                }
                if(parseInt(vm.StorageFileId)==parseInt(atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''))))
                {
                    vm.pageNo;
                }
                else{
                    vm.pageNo=1;
                }
                const currentDate = new Date();
                const formattedCurrentDate = new Date(currentDate.toDateString().toWellFormed()).getTime(); // Convert to milliseconds
                const expirationDate = new Date(vm.ExpirationDate).getTime();
                var params = `folderId=${vm.StorageFileId}&pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&searchFilter&sortBy&sortExp&refCode=Public`;
                DataService.GetPubLicLinkFolderList(params).then((response) => {
                    debugger;
                    if (isNaN(expirationDate) || expirationDate >= formattedCurrentDate) {
                        vm.folderFileListing = response.data.FolderData;
                    } else {
                        vm.checkPublicLinkExpired = true;
                        var params = `folderId=${vm.StorageFileId}&refCode=Public`;
                        DataService.GetStorageFolderDetails(params).then(() => {
                            alert('hi');
                        });
                    }
    
                    if (vm.folderFileListing.length > 0) {
                        vm.totalRecords = vm.folderFileListing[0].totalRecords;
                    }
                    
                    setTimeout(function () {
                        vm.ResponsiveDataTable('div_forListView')
                    }, 500);
                    setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                    vm.BreadCrumb("MySpace",vm.StorageFileId)
                })
            },
            GetThumbnail: function(path){
                var cls=null;
                if(path!=null){
                    var incStr = path.includes("wwwroot"); 
                }
                if(incStr==true){
                     cls=path.replace("/wwwroot", "");
                }
                else{
                    cls=path;
                }
                return cls;
            },
            DownloadSingleFiles: function (fileId, fileName) {
                var vm = this;
                var IsVersion = 0
                var params = `fileId=${fileId}&refCode=PublicDocFile`
                DataService.DownloadPublicFileSingle(params).then((response) => {
                    var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
    
                })
            },
            DownloadFile() {
                var vm = this;
                var FolderName=window.location.search.split("FolderName")[1];
                var params = `storageUniqueCode=${window.location.search.split("UniqueCode=")[1]}`
                DataService.GetPublicallyStorageDocs(params).then((response) => {
                    var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', decodeURIComponent(decodeURIComponent(FolderName)) + ".zip");
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            },
            pagerMethod: function (s) {
                this.pageNo=s.PageNumber;
                this.pageSize=s.PageSize;
                if (parseInt(this.pageNo) > 0) {
                  this.GetFolderList(this.StorageFileId);
                }
                else {
                    this.pageNo = 1
                }
            },
             openLightbox: function () {
                var vm = this;
                vm.visibleRef = true
            },
            onHide: function () {
                var vm = this;
                vm.visibleRef = false
            },
            pageSizecommon: function (e) {
                var vm = this;
                vm.pageSize = e.target.value
                this.GetFolderList();
            },
            GetFileIconClass: function (ext) {
                ext=ext.toLowerCase();
                var fileclass = "";
                if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif") {
                    fileclass = "fa fa-file-image-o";
                }
                if (ext == "pdf") {
                    fileclass = "fa fa-file-pdf-o"
                }
                if (ext == "xlsx") {
                    fileclass = "fa fa-file-excel-o"
                }
                if (ext == "txt") {
                    fileclass = "fa fa-file-text-o"
                }
                if (ext == "xls") {
                    fileclass = "fa fa-file-excel-o"
                }
                if (ext == "docx") {
                    fileclass = "fa fa-file-word-o"
                }
                return fileclass
            },
            convertmbtoKb: function (val) {
                var convertKb = 0;
                if (val == 0) {
                    convertKb = '0 Bytes'
                } else
                    if (val < 1) {
                        convertKb = val.toFixed(2) + ' KB'
                    }
                    else {
                        convertKb = val.toFixed(2) + ' MB'
                    }
                return convertKb;
            },
            BreadCrumb(name, id) {
                var vm = this;
                if (id != undefined) {
                    vm.folderId = id;
                }
                vm.parentname = []
                vm.commonParentName = name;
                if (vm.folderId == undefined || vm.folderId == "") {
                    vm.folderId = 0;
                }
                DataService.GetPublicLinkFolderBreadCrumb(vm.folderId).then((response) => {
                    var numbersString = response.data;
                    var numbersArray = numbersString.split('>');
                    numbersArray.forEach((value, index) => {
                        vm.parentname.push(value.split("~"))
                    })
                    vm.closepopup;
                    DataService.GetParentFolderBreadCrumb(vm.folderId).then((response) => {
                        vm.breadcrumbForManagePopup = response.data;
                    })
                });
    
            },
            SpecificFolder: function (id, name) {
                var vm = this;
                var scondition = "";
                vm.GetFolderList(id)
            },
        }
    }
    </script>
    <style>
    body{padding:0px;}
    footer{display: none;}
    header{display: none;}
    #divjuda_menu{display: none;}
    body {display: flex;justify-content: center;align-items: center; height: 100vh;margin: 0;}
    .content {text-align: center;}
    /* .p-action-btn.new em { font-size: 20px!important;  padding-top: 5px;}
    .p-action-btn.new span { font-size:14px!important;  padding-top: 5px; float:left;}
    .p-action-btn.new:hover{color: #fff !important;background: #8d9599;}
    .theme-light-shade{background: #f5f5f5;}
    .theme-light-shade .p-action-btn {border: none;}
    .img-icons.new em:before{top: 25px;} */
     </style>